import Vue from 'vue'
import App from './App.vue'
import ElementUI from 'element-ui';
// 需要引入element ui的样式
import 'element-ui/lib/theme-chalk/index.css';
// import 'font-awesome/css/font-awesome.min.css'
import router from './router/index.js'
// // 引入Vuex的存储
// import store from './store'

import {postRequest,putRequest,deleteRequest,getRequest,postKeyValueRequest} from "./utils/request";

// axios.defaults.baseURL = '/'

// ----------------------------------------------------------
// 在每次路由之前，都需要进行判断
// 如果是 / 那么就放行
//   否则 验证用户是否登录了，如果登录了就初始化菜单，如果菜单初始化了，那么就不再初始化
//        如果用户没有登录，那么就重定向
router.beforeEach((to, from, next) => {
  if (to.path == '/' || to.path == '/register') {
    next();
  }else {
    if (window.sessionStorage.getItem("user")) {
      // 如果用户登录了，就初始化菜单。
      // 初始化菜单时，就会出事 this.$router.router
      next();
    }else{
      next('/?redirect='+to.path);
    }
  }
})
// ----------------------------------------------------------


// 安装 ElementUI -- 需要导入ElementUI,和指定使用，不然element ui不生效
Vue.use(ElementUI,{size:'small'});

Vue.config.productionTip = false


new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
