import Vue from 'vue'
import Router from 'vue-router'
import Login from '../components/Login.vue'
import Home from '../components/Home.vue'
import Register from '../views/base-manager/user-manager/register'

// 安装路由，底层会调用 Router的install方法
Vue.use(Router)

const routes = [
    {
        path: '/',
        name: 'Login',
        component: Login,
        hidden:true
    },
    {
        path: '/home',
        name: 'Home',
        component: Home,
        hidden:true,
        meta:{
            roles:['admin','user']
        },
        children: [
            {
                path: '/heal',
                name: '首页',
                hidden: false,
                component: { render: c => c("router-view")},
                meta:{
                    roles:['admin','user']
                },
                children: [
                    {
                        path: 'info',
                        name: '健康信息管理',
                        component: () => import('../views/base-manager/health-manager/info'),
                        hidden:true,
                        meta:{
                            roles:['admin','user']
                        },
                    }
                ]
            },
            {
                path: '/base',
                name: '基础管理',
                component: { render: c => c("router-view")},
                hidden: false,
                meta:{
                    roles:['admin','user']
                },
                children: [
                    {
                        path: 'bazaars',
                        name: '市场管理',
                        component: () => import('../views/base-manager/bazaar-manager/list.vue'),
                        hidden:true,
                        meta:{
                            roles:['admin']
                        }
                    },
                    {
                        path: 'stalls',
                        name: '摊位管理',
                        component: () => import('../views/base-manager/stall-manager/list.vue'),
                        hidden:true,
                        meta:{
                            roles:['admin']
                        }
                    },
                    {
                        path: 'apply/manager/list',
                        name: '摊位申请列表',
                        component: () => import('../views/base-manager/stall-manager/managerApplyList'),
                        hidden:true,
                        meta:{
                            roles:['admin']
                        }
                    },
                    {
                        path: 'apply',
                        name: '摊位申请',
                        component: () => import('../views/base-manager/stall-manager/apply'),
                        hidden:true,
                        meta:{
                            roles:['user']
                        }
                    },
                    {
                        path: 'apply/list',
                        name: '摊位申请列表',
                        component: () => import('../views/base-manager/stall-manager/userApplyList'),
                        hidden:true,
                        meta:{
                            roles:['user']
                        }
                    }
                ]
            },
            {
                path: '/identity',
                name: '人员信息管理',
                component: { render: c => c("router-view")},
                hidden: false,
                meta:{
                    roles:['admin']
                },
                children: [
                    {
                        path: 'userManager',
                        name: '用户管理',
                        component: () => import('../views/base-manager/user-manager/list'),
                        hidden:true,
                        meta:{
                            roles:['admin']
                        }
                    }
                ]
            },
            {
                path: '/inform',
                name: '消息管理',
                component: { render: c => c("router-view")},
                hidden: false,
                meta:{
                    roles:['admin','user']
                },
                children: [
                    {
                        path: 'message',
                        name: '系统公告',
                        component: () => import('../views/base-manager/message-manager/managerList'),
                        hidden:true,
                        meta:{
                            roles:['admin']
                        },
                    },
                    {
                        path: 'notice',
                        name: '系统通知',
                        component: () => import('../views/base-manager/message-manager/userList'),
                        hidden:true,
                        meta:{
                            roles:['user']
                        },
                    }
                ]
            }
        ]
    },
    {
        path: '/register',
        name: 'register',
        component: Register,
        hidden:true
    },


];

const router = new Router({
    routes,
    //mode: "history"  // 使用 history模式
    // 如果想要使用 history默认，自动刷新时报404，解决方法：在nginx中配置信息或者不使用
});


export default router;