<template>
    <div>
        <el-form
                :rules="rules"
                ref="loginForm"
                v-loading="loading"
                element-loading-text="正在登录..."
                element-loading-spinner="el-icon-loading"
                element-loading-background="rgba(0, 0, 0, 0.8)"
                :model="loginForm"
                class="loginContainer">
            <h3 class="loginTitle">市场管理后台系统</h3>
            <el-form-item prop="username" >
                <el-input size="normal" type="text" v-model="loginForm.username" auto-complete="off"
                          placeholder="请输入用户名"></el-input>
            </el-form-item>
            <el-form-item prop="password">
                <el-input size="normal" type="password" v-model="loginForm.password" auto-complete="off"
                          placeholder="请输入密码" @keydown.enter.native="submitLogin"></el-input>
            </el-form-item>
            <el-checkbox size="normal" class="loginRemember" v-model="checked"></el-checkbox>
            <el-button size="normal" type="primary" style="width: 100%;" @click="submitLogin">登录</el-button>
            <el-button size="normal" type="text" style="width: 100%;" @click="submitRegister">注册用户</el-button>
        </el-form>
    </div>
</template>
<script>
    import {postRequest} from "../utils/request";
    export default {
        name: "Login",
        data() {
            return {
                loading: false,
                loginForm: {
                    username: 'SmsAdmin',
                    password: 'password'
                },
                checked: true,
                // :rules 工作机制
                rules: {
                    username: [{required: true, message: '请输入用户名', trigger: 'blur'}],
                    password: [{required: true, message: '请输入密码', trigger: 'blur'}]
                }
            }
        },
        methods: {
            submitLogin() {
                /**
                 *  this.$refs 获取所有组件上存在 ref属性的元素
                 *  this.$refs.loginForm 获取指定ref名称的元素的DOM对象
                 *
                 *  理解 validate 函数:  底层是通过 async-validator模块来实现的,可以参考官方文档。
                 *   validate 函数的定义如下:
                 *      Function(callback: Function(boolean, object)): Promise   如果不指定回调函数，系统就返回 Promise对象
                 *                                  boolean 表示是否校验成功
                 *                                  object  表示未通过校验的字段
                 *
                 *
                 *
                 */
                this.$refs.loginForm.validate((valid) => {
                    // 如果校验成功
                    if (valid) {
                        this.loading = true;
                        postRequest('/admin/login', this.loginForm).then(resp => {
                            this.loading = false;
                            if (resp) {
                                // // 将当前的HR信息存放到store中
                                // this.$store.commit('INIT_CURRENTHR', resp.obj);
                                // 前端存储的方式 window.sessionStorage  JSON.stringify将javascript对象，转换成json字符串
                                window.sessionStorage.setItem("user", JSON.stringify(resp.data));
                                // 如果指定了重定向的位置，那么就直接跳转过去，没有就默认是/home
                                let path = this.$route.query.redirect;
                                this.$router.replace("/home")
                            }
                        })
                    } else {
                        // 会自动打印吗？
                        this.$message.error('请输入所有字段');
                        return false;
                    }
                });
            },
            submitRegister() {
                // 这里是路由跳转
                this.$router.replace("/register")
            }
        }
    }
</script>

<style>
    .loginContainer {
        border-radius: 15px;
        background-clip: padding-box;
        margin: 180px auto;
        width: 350px;
        padding: 15px 35px 15px 35px;
        background: #fff;
        border: 1px solid #eaeaea;
        box-shadow: 0 0 25px #cac6c6;
    }

    .loginTitle {
        margin: 15px auto 20px auto;
        text-align: center;
        color: #505458;
    }

    .loginRemember {
        text-align: left;
        margin: 0px 0px 15px 0px;
    }
</style>