<template>
    <div>
        <el-container>

            <el-header class="homeHeader">
                <div class="title">摊位后台管理系统</div>
                <div>
                    <!--dropdown 下拉菜单  @command 表示该属性对应的值，会传入指定的方法中 -->
                    <el-dropdown class="userInfo" @command="commandHandler">
                        <!-- 显示名字和头像 -->
                        <span class="el-dropdown-link">{{user.username}}</span>
                        <!-- 设置显示-->
                        <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item command="userInfo">个人中心</el-dropdown-item>
                            <el-dropdown-item command="setting">设置</el-dropdown-item>
                            <el-dropdown-item command="logout" divided>注销登录</el-dropdown-item>
                        </el-dropdown-menu>
                    </el-dropdown>
                </div>
            </el-header>
            <el-container>
                <!--   侧边栏容器 -->
                <el-aside width="200px">
                    <el-menu router unique-opened>
                        <!--菜单选项  使用 router 后，允许index的值作为跳转路径-->
                        <el-submenu :index="item.path +''" v-for="(item,index) in routes" v-if="!item.hidden && item.meta.roles.indexOf(user.userType) > -1" :key="index">
                            <template slot="title">
                                <span>{{item.name}}</span>
                            </template>
                            <el-menu-item :index="item.path+ '/' + child.path"  v-if="child.meta.roles.indexOf(user.userType) > -1" v-for="(child,indexj) in item.children" :key="indexj">
                                {{child.name}}
                            </el-menu-item>
                        </el-submenu>
                    </el-menu>
                </el-aside>
                <!--   主题内容  this.$router.currentRoute == this.$route  -->
                <el-main>
                    <!--页面导航  首页>基本信息  如果当前路由的路径是/home是,就不显示该导航 -->
                    <el-breadcrumb separator-class="el-icon-arrow-right" v-if="this.$route.path != '/home'" >
                        <!-- 点击返回首页-->
                        <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>

                        <el-breadcrumb-item>{{this.$route.name}}</el-breadcrumb-item>
                    </el-breadcrumb>
                    <!-- 主页时，显示 -->
                    <div class="homeWelcome" v-if="this.$route.path =='/home'">
                        欢迎来到摊位后台管理系统！
                    </div>
                    <!-- 模块显示 -->
                    <router-view class="homeRouterView"/>
                </el-main>
            </el-container>
        </el-container>
    </div>
</template>

<script>
    import {getRequest} from "../utils/request";

    export default {
        // 名称
        name: "Home",
        // 模板中使用的数据
        data() {
            return {
                // 将一个json字符串转换成一个javaScript对象
                user: JSON.parse(window.sessionStorage.getItem("user"))
            }
        },
        // 计算数据，方法的形式定义，调用使用属性的形式
        computed: {
            routes() {
                // 初始化菜单时，获取的
                console.log(this.$router.options.routes)
                return this.$router.options.routes[1].children;
            }
        },
        // 方法
        methods: {
            //只实现了退出功能
            commandHandler(cmd) {
                if (cmd == 'logout') {
                    // this.$confirm 确认框
                    this.$confirm('此操作将注销登录, 是否继续?', '提示', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                    }).then(() => {
                        getRequest("/admin/logout").then(res => {
                            // 退出登录，移除缓存
                            window.sessionStorage.removeItem("user")
                            this.$router.replace("/");
                        })
                    }).catch(() => {
                        this.$message({
                            type: 'info',
                            message: '已取消操作'
                        });
                    });
                }
            }

        }
    }
</script>

<style>
    .homeRouterView {
        margin-top: 10px;
    }

    .homeWelcome {
        text-align: center;
        font-size: 30px;
        font-family: 华文行楷;
        color: #409eff;
        padding-top: 50px;
    }

    .homeHeader {
        background-color: #409eff;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 0px 15px;
        box-sizing: border-box;
    }

    .homeHeader .title {
        font-size: 30px;
        /*font-family: 华文行楷;*/
        color: #ffffff
    }

    .homeHeader .userInfo {
        cursor: pointer;
    }

    .el-dropdown-link img {
        width: 48px;
        height: 48px;
        border-radius: 24px;
        margin-left: 8px;
    }

    .el-dropdown-link {
        display: flex;
        align-items: center;
    }
</style>