<template>
    <div>
        <el-form ref="form" class="loginContainer"  :model="formData"  :rules="rules" label-width="200px" size="medium">
            <el-form-item label="用户账号" prop="code" >
                <el-input v-model="formData.code" style="width: 350px"></el-input>
            </el-form-item>
            <el-form-item label="用户名称" prop="name" >
                <el-input v-model="formData.name"  style="width: 350px"></el-input>
            </el-form-item>
            <el-form-item label="密码" prop="password" >
                <el-input type="password" v-model="formData.password" autocomplete="off" style="width: 350px"></el-input>
            </el-form-item>
            <el-form-item label="确认密码" prop="checkPassword" >
                <el-input type="password" v-model="formData.checkPassword" autocomplete="off" style="width: 350px"></el-input>
            </el-form-item>
            <el-form-item label="电话号码" prop="phone" >
                <el-input v-model="formData.phone"  style="width: 350px"></el-input>
            </el-form-item>
            <el-form-item  label="邮箱" prop="email" >
                <el-input v-model="formData.email"  style="width: 350px"></el-input>
            </el-form-item>
            <el-form-item  label="身份证号" prop="idCard" >
                <el-input v-model="formData.idCard" style="width: 350px"></el-input>
            </el-form-item>
            <el-form-item  label="描述" prop="description" >
                <el-input v-model="formData.description" type="textarea"   rows="3" style="width: 350px"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary"  @click="doCreate">立刻注册</el-button>
                <el-button type="primary"  @click="returnView">返回</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    import {createUser} from "../../../api/userApi";

    export default {
        name: "register",
        data() {
            const validatePass = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error('请输入密码'));
                } else {
                    if (this.formData.checkPassword !== '') {
                        this.$refs.ruleForm.validateField('checkPassword');
                    }
                    callback();
                }
            };
            const validatePass2 = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error('请再次输入密码'));
                } else if (value !== this.formData.password) {
                    callback(new Error('两次输入密码不一致!'));
                } else {
                    callback();
                }
            };
            return {
                formData: {
                    id: null,
                    code: null,
                    name: null,
                    password: null,
                    checkPassword: null,
                    phone: null,
                    email: null,
                    idCard: null,
                    isManager: null,
                    description: null
                },

                rules: {
                    password: [
                        { validator: validatePass, trigger: 'blur' }
                    ],
                    checkPassword: [
                        { validator: validatePass2, trigger: 'blur' }
                    ]
                }
            }
        },
        methods: {

            initFormData(row) {
                if (row != null) {
                    this.formData = {
                        id: row.id,
                        code: row.code,
                        name: row.name,
                        password: row.password,
                        phone: row.phone,
                        email: row.email,
                        idCard: row.idCard,
                        isManager: row.isManager,
                        description: row.description
                    }
                } else {
                    this.formData = {
                        id: null,
                        code: null,
                        name: null,
                        password: null,
                        phone: null,
                        email: null,
                        idCard: null,
                        isManager: null,
                        description: null
                    }
                }

            },
            doCreate() {
                createUser(this.formData).then(res => {
                    this.initFormData()
                    this.$message({
                        message: '注册成功,并返回登录页面',
                        type: 'success'
                    });
                    setTimeout(() => {}, 2000);
                    this.$router.replace("/home")
                })
            },
            returnView() {
                this.$router.replace("/home")
            }
        }
    }
</script>

<style scoped>
    .loginContainer {
        border-radius: 15px;
        background-clip: padding-box;
        margin: 180px auto;
        width: 700px;
        padding: 50px 35px 15px 35px;
        background: #fff;
        border: 1px solid #eaeaea;
        box-shadow: 0 0 50px #cac6c6;
    }
</style>