import {postRequest,putRequest,deleteRequest,getRequest,postKeyValueRequest} from "../utils/request";

export const listUser = (params) => {
    return postRequest("/uiapi/v1/user/list", params)
}

export const createUser = (params) => {
    return postRequest("/uiapi/v1/user/register", params)
}

export const deleteUser = (params) => {
    return deleteRequest("/uiapi/v1/user/delete", params)
}

export const updateUser = (params) => {
    return putRequest("/uiapi/v1/user/update", params)
}